.info {
  &__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    @include screen(tablet) {
      flex-direction: row;
      align-items: flex-start;
    }
  }
  &__item {
    h3 {
      margin-top: 24px;
      color: $brown;
      font-size: 20px;
      font-weight: 500;
      line-height: 130%;
      @include screen(tablet) {
        font-size: 24px;
      }
      @include screen(laptop) {
        font-size: 32px;
      }
    }
    p {
      margin-top: 8px;
      color: $brown;
      font-variant-numeric: lining-nums proportional-nums;
      font-size: 14px;
      font-weight: 500;
      line-height: 130%;
      letter-spacing: 0.096px;
      @include screen(tablet) {
        font-size: 16px;
      }
    }
    &-left {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include screen(tablet) {
        align-items: flex-start;
      }
      .info__image {
        @include screen(laptop) {
          margin-left: 40px;
        }
      }
    }
    &-right {
      display: flex;
      flex-direction: column;
      align-items: center;
      @include screen(tablet) {
        align-items: flex-end;
      }
      .info__image {
        @include screen(laptop) {
          margin-right: 40px;
        }
      }
    }
  }
  &__image {
    max-width: 200px;
    @include screen(tablet) {
      max-width: 240px;
    }
    @include screen(laptop) {
      max-width: 372px;
    }
    @include screen(desktop) {
      max-width: 390px;
    }
    img {
      width: 100%;
    }
  }
  &__arrow {
    margin-top: 24px;
    display: none;
    max-width: 114px;
    @include screen(tablet) {
      display: block;
    }
    @include screen(laptop) {
      max-width: 100%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 64px;
    svg {
      height: 18px;
      @include screen(tablet) {
        height: 32px;
      }
      @include screen(laptop) {
        height: 40px;
      }
    }
    @include screen(tablet) {
      margin: -50px auto 0;
    }
    @include screen(laptop) {
      max-width: 552px;
    }
  }
  &__title {
    text-align: center;
    color: $brown;
    font-size: 28px;
    font-weight: 600;
    line-height: 130%;
    letter-spacing: 0.36px;
    margin-bottom: 28px;
    @include screen(tablet) {
      margin-bottom: 44px;
      font-size: 36px;
    }
    @include screen(laptop) {
      font-size: 50px;
    }
  }
  &__prop {
    width: 100%;
    background-color: $green;
    border-radius: 24px;
    font-size: 16px;
    font-family: 'Golos Text';
    padding: 16px 12px;
    text-align: center;
    @include screen(tablet) {
      font-size: 20px;
      padding: 20px;
    }
    @include screen(laptop) {
      font-size: 24px;
    }
    @include screen(desktop) {
      padding: 20px 32px;
    }
  }
}