.header {
  padding: 32px 0;
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 100;
  @include screen(laptop) {
    padding: 50px 0;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav {
    display: flex;
    align-items: center;
    gap: 28px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: none;
    @include screen(tablet) {
      display: flex;
    }
    @include screen(laptop) {
      gap: 40px;
    }
  }
  &__logo {
    font-family: 'South';
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.006em;
    color: $brown;
    display: none;
    @include screen(tablet) {
      display: block;
    }
    @include screen(laptop) {
      font-size: 24px;
    }
  }
  &__link {
    font-size: 14px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 0.006em;
    border-bottom: 2px solid transparent;
    color: $brown;
    @include screen(laptop) {
      transition: border-color $transition-default;
      font-size: 20px;
      &:hover {
        border-color: $black;
      }
    }
  }
  &__phone {
    color: $brown;
    font-size: 14px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 0.006em;
    border-bottom: 2px solid transparent;
    font-variant-numeric: lining-nums proportional-nums;
    @include screen(laptop) {
      transition: border-color $transition-default;
      font-size: 20px;
      &:hover {
        border-color: $black;
      }
    }
  }
}