.hero {
  position: relative;
  &__bg {
    position: absolute;
    min-height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 310px;
    @include screen(tablet) {
      min-height: 600px;
    }
    @include screen(laptop) {
      min-height: 100vh;
    }
  }
  &__title {
    z-index: 2;
    position: relative;
    font-family: 'South';
    font-weight: 400;
    letter-spacing: 0.006em;
    white-space: nowrap;
    color: $brown;
    font-size: 40px;
    @include screen(tablet) {
      font-size: 80px;
    }
    @include screen(laptop) {
      font-size: 160px;
    }
    @include screen(desktop) {
      font-size: 200px;
    }
  }
}