.products {
  padding-top: 64px;
  position: relative;
  @include screen(tablet) {
    padding-top: 120px;
  }
  @include screen(laptop) {
    padding-top: 160px;
  }
  @include screen(laptop) {
    padding-top: 168px;
  }
  &__title {
    margin-bottom: 28px;
    color: $brown;
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    line-height: 130%;
    letter-spacing: 0.168px;
    @include screen(tablet) {
      font-size: 32px;
      margin-bottom: 44px;
    }
    @include screen(laptop) {
      font-size: 48px;
      margin-bottom: 60px;
    }
  }
  &__buttons {
    margin: 0 auto 28px;
    display: grid;
    max-width: 264px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    background-color: $pink;
    border-radius: 12px;
    overflow: hidden;
    transition: background-color $transition-default;
    @include screen(tablet) {
      margin: 0 auto 44px;
    }
    @include screen(laptop) {
      margin: 0 auto 60px;
      &:hover {
        background-color: $pink-500;
      }
    }
  }
  &__button {
    transition: background-color $transition-default, color $transition-default;
    color: $brown;
    font-size: 24px;
    padding: 16px 20px;
    font-weight: 500;
    line-height: 135%;
    border-radius: 12px;
    letter-spacing: 0.144px;
    @include screen(laptop) {
      &:hover {
        background-color: $pink-500;
      }
    }
    &.active {
      background-color: $dark-brown;
      color: $white;
    }
  }
  &__container {
    display: none;
    column-gap: 24px;
    row-gap: 24px;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      column-gap: 25px;
      row-gap: 60px;
    }
    @include screen(desktop) {
      column-gap: 60px;
      row-gap: 73px;
    }
    &.active {
      display: grid;
    }
  }
  &__item {
    border-radius: 24px;
    padding-bottom: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
    &-green {
      background-color: $green;
    }
    &-pink {
      background-color: $pink;
    }
    &-yellow {
      background-color: $yellow;
    }
    &__left,
    &__right {
      background-color: #F8F4F3;
      position: absolute;
      right: 0;
      width: 51px;
      bottom: 0;
      height: 94px;
      z-index: 2;
    }
    &__left {
      left: 0;
      padding: 18px 30px;
      border-radius: 0 24px 0 0;
      &:before,
      &:after {
        content: '';
        display: block;
        width: 24px;
        height: 24px;
        transform: rotate(90deg);
        border: 1px solid;
        position: absolute;
        border-radius: 0 0 24px 0;
        border-color: transparent;
        box-shadow: 7px 7px 0 6px #F8F4F3;
      }
      &:before {
        left: 100%;
        bottom: 0;
      }
      &:after {
        left: 0;
        bottom: 100%;
      }
    }
    &__right {
      right: 0;
      padding: 19px 30px;
      border-radius: 24px 0 0 0;
      &:before,
      &:after {
        content: '';
        display: block;
        width: 24px;
        height: 24px;
        border: 1px solid;
        position: absolute;
        border-radius: 0 0 24px 0;
        border-color: transparent;
        box-shadow: 7px 7px 0 6px #F8F4F3;
      }
      &:before {
        right: 100%;
        bottom: 0;
      }
      &:after {
        right: 0;
        bottom: 100%;
      }
    }
    &__title {
      color: $brown;
      font-size: 24px;
      font-weight: 400;
      line-height: 135%;
      text-align: center;
      font-family: 'Golos Text';
      letter-spacing: 0.144px;
      max-width: 204px;
      margin: 0 auto 8px;
      @include screen(laptop) {
        max-width: 320px;
      }
    }
    &__price {
      color: $brown;
      font-family: 'Golos Text';
      font-size: 28px;
      text-align: center;
      font-weight: 600;
      line-height: 135%;
      letter-spacing: 0.168px;
    }
  }
  &__more {
    border: 1px solid #282828;
    display: inline-flex;
    padding: 20px;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    color: $brown;
    font-size: 24px;
    width: 100%;
    font-weight: 500;
    line-height: 130%;
    letter-spacing: 0.24px;
    position: relative;
    left: 50%;
    letter-spacing: 0.192px;
    transform: translateX(-50%);
    margin-top: 36px;
    @include screen(tablet) {
      padding: 20px 44px;
      width: auto;
      margin-top: 72px;
    }
    @include screen(laptop) {
      font-size: 32px;
      padding: 24px 44px;
      margin-top: 100px;
      transition: color $transition-default, background-color $transition-default;
      &:hover {
        color: $white;
        background-color: $brown;
      }
    }
  }
}