.where {
  padding-top: 64px;
  @include screen(tablet) {
    padding-top: 120px;
  }
  @include screen(laptop) {
    padding-top: 160px;
  }
  @include screen(desktop) {
    padding-top: 200px;
  }
  &__title {
    color: $brown;
    margin-bottom: 28px;
    font-size: 28px;
    text-align: center;
    font-weight: 600;
    line-height: 130%;
    letter-spacing: 0.288px;
    @include screen(tablet) {
      margin-bottom: 44px;
      font-size: 32px;
    }
    @include screen(laptop) {
      font-size: 48px;
      margin-bottom: 60px;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 24px;
    @include screen(tablet) {
      flex-direction: row;
    }
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 71px;
    }
  }
  &__image {
    img {
      max-height: 32px;
      @include screen(laptop) {
        max-height: 41px;
      }
    }
  }
  &__item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    background-color: #EEE7E5;
    padding: 32px 44px;
    position: relative;
    @include screen(tablet) {
      width: auto;
    }
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: #dbd4d2;
      }
    }
    span {
      color: $brown;
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      letter-spacing: 0.144px;
      @include screen(laptop) {
        font-size: 24px;
      }
      @include screen(laptop) {
        font-size: 32px;
        letter-spacing: 0.192px;
      }
    }
  }
  &__arrow {
    position: absolute;
    right: 100%;
    top: 40px;
    min-width: 128px;
    display: none;
    @include screen(tablet) {
      display: flex;
      justify-content: flex-end;
    }
    @include screen(laptop) {
      top: 100%;
      right: 20px;
    }
    @include screen(desktop) {
      right: 100px;
    }
    &-mob {
      display: block;
      @include screen(laptop) {
        display: none;
      }
    }
    &-pc {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
}