.footer {
  padding: 64px 0 32px;
  @include screen(laptop) {
    padding: 120px 0 48px;
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'South';
    font-size: 16px;
    font-weight: 400;
    line-height: 37px;
    color: $brown;
    letter-spacing: 0.006em;
    @include screen(tablet) {
      font-size: 24px;
    }
    @include screen(laptop) {
      font-size: 32px;
    }
  }
}