.work-with-us {
  padding-top: 64px;
  position: relative;
  &__bg {
    position: absolute;
    min-height: 100%;
    width: 100%;
    display: none;
    @include screen(laptop) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  @include screen(tablet) {
    padding-top: 120px;
  }
  @include screen(laptop) {
    padding-top: 160px;
  }
  @include screen(laptop) {
    padding-top: 168px;
  }
  &__title {
    margin-bottom: 28px;
    color: $brown;
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    line-height: 130%;
    letter-spacing: 0.168px;
    @include screen(tablet) {
      margin-bottom: 44px;
      font-size: 32px;
    }
    @include screen(laptop) {
      font-size: 48px;
      margin-bottom: 60px;
    }
  }
  &__separators {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    svg {
      height: 18px;
      @include screen(tablet) {
        height: 24px;
      }
    }
  }
  &__items {
    position: relative;
    z-index: 2;
    margin: 0 auto 36px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    @include screen(tablet) {
      flex-direction: row;
      max-width: 443px;
      margin: 0 auto 80px;
    }
    @include screen(laptop) {
      max-width: 840px;
    }
  }
  &__item {
    width: 100%;
    background-color: $green;
    border-radius: 24px;
    font-size: 16px;
    padding: 16px 12px;
    font-family: 'Golos Text';
    text-align: center;
    color: $brown;
    font-size: 16px;
    font-weight: 400;
    line-height: 135%;
    letter-spacing: 0.144px;
    &:nth-of-type(1) {
      border-radius: 32px 32px 8px 8px;
    }
    &:nth-of-type(2) {
      border-radius: 8px 8px 8px 32px;
    }
    &:nth-of-type(3) {
      border-radius: 8px 32px 8px 8px;
    }
    &:nth-of-type(4) {
      border-radius: 8px 8px 32px 32px;
    }
    @include screen(tablet) {
      font-size: 20px;
      padding: 20px;
    }
    @include screen(laptop) {
      font-size: 24px;
    }
    @include screen(desktop) {
      padding: 20px 32px;
    }
  }
  &__more {
    border: 1px solid #282828;
    display: inline-flex;
    padding: 20px 44px;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    color: $brown;
    font-size: 24px;
    font-weight: 500;
    line-height: 130%;
    letter-spacing: 0.24px;
    position: relative;
    left: 50%;
    letter-spacing: 0.192px;
    transform: translateX(-50%);
    @include screen(tablet) {
      font-size: 32px;
      padding: 24px 44px;
    }
    @include screen(laptop) {
      font-size: 40px;
      transition: color $transition-default, background-color $transition-default;
      &:hover {
        color: $white;
        background-color: $brown;
      }
    }
  }
}